.chat-container{overflow:hidden;border:1px solid #000;margin:0 auto;width:90%;max-width:1200px;border-radius:var(--radius);background-color:var(--background)}.chat__box--container{display:flex;flex-direction:column;max-height:70vh}.chat__box{overflow:auto;display:flex;flex-direction:column-reverse}.chat__box--messages{display:flex;flex-direction:column;grid-gap:1em;gap:1em;padding:1em;background-image:var(--bg-chat-img)}.message__container--left{justify-content:flex-start}.message__container--right{justify-content:flex-end}.message--left{border-radius:0 20px 20px 20px;background-color:var(--clr-primary)}.message--right{border-radius:20px 0 20px 20px;background-color:var(--clr-accent);color:var(--clr-light)}.message--right .message__user{display:none}.message__container{display:flex}.chat__message{border:1px solid #000;padding:.5em 1em;max-width:90%;display:flex;flex-direction:column;grid-gap:.3em;gap:.3em;overflow-wrap:break-word}.chat__message .message__user{font-weight:bold;font-family:var(--font-title)}.chat__message .message__content{padding:0 .3em}.chat__message .message__timestamp{font-size:.7rem;opacity:.75}.send-message{background-color:var(--clr-secondary);padding:.5em}.send-message .send-message__form{display:flex;grid-gap:.5em;gap:.5em;justify-content:space-between}.send-message .send-message__input{min-width:0;flex-grow:1;padding:.5em;border:none;border-radius:30px;background-color:var(--background);color:inherit;outline:none}.send-message .send-message__button{background-color:var(--clr-accent);color:var(--clr-light);padding:0 1em;border:none;border-radius:30px;font-family:inherit;font-weight:bold;cursor:pointer}.send-message .send-message__button .send-svg{width:23px;aspect-ratio:1;vertical-align:bottom}.send-message .send-message__button:hover,.send-message .send-message__button:focus{opacity:.75}.chat__aside{overflow:auto;max-height:70vh;display:flex;flex-direction:column;grid-gap:1px;gap:1px}.chat__aside .aside__user{padding:1em;display:flex;grid-gap:.5em;gap:.5em;align-items:center;background-color:var(--clr-primary)}.chat__aside .aside__user .online-circle{border:1px solid lime;background-color:lime;border-radius:50px;color:lime;width:.7rem;aspect-ratio:1}.chat__aside .aside__user .aside__user--email{font-size:.7rem;overflow:hidden;text-overflow:ellipsis}@media(min-width: 700pX){.chat__message{max-width:70%}}@media(min-width: 1000pX){.chat__message{max-width:45%}.chat__aside{width:20%}.chat__box--container{width:80%}}
.form{padding:1em 2em;background-color:var(--background);border-radius:var(--radius)}.form h2{text-align:center;color:var(--foreground);font-family:var(--font-title)}.form .form-element{margin:var(--margin-top-element) 0}.form .form-element label{color:var(--foreground)}.form .form-element input{padding:.7em;display:block;width:100%;border:1px solid var(--clr-primary);border-radius:var(--radius)}.form .form-element button{width:100%}.form .invalid-message{color:#ff3c3c;font-size:.8rem}
